<template>
  <div>
    <h1>randomuser</h1>
    <input type="text" v-model="numOfUsers">
    <select v-model="gender">
      <option value="">None</option>
      <option value="male">Male</option>
      <option value="female">Female</option>
      </select>
    <button v-if="!loading" @click="getUsers(numOfUsers, gender)">Get random users</button>
    <p v-if="loading">loading...</p>
    <p v-if="error">error</p>
  </div>
  <div v-for="user in users" :key="user">
    <img :src="user.picture.large">
    <h2>{{ user.name.title }} {{ user.name.first }} {{ user.name.last }}</h2>
    <p>{{ user.gender }}</p>
    <p>{{ user.email }}</p>
    <p>{{ user.location.timezone.description }}</p>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import useApi from '@/composables/use-api';

export default defineComponent({
  setup() {
    const loading = ref(false);
    const error = ref(false);
    const { get } = useApi();
    const users = ref('');
    const numOfUsers = ref(1);
    const gender = ref('');

    async function getUsers(num: number, gen: string) {
      loading.value = true;
      error.value = false;
      try {
        users.value = (await get(num, gen));
      } catch {
        error.value = true;
      } finally {
        loading.value = false;
      }
    }

    return {
      loading, error, getUsers, users, numOfUsers, gender,
    };
  },
});
</script>
