
import { defineComponent, ref } from 'vue';
import useApi from '@/composables/use-api';

export default defineComponent({
  setup() {
    const loading = ref(false);
    const error = ref(false);
    const { get } = useApi();
    const users = ref('');
    const numOfUsers = ref(1);
    const gender = ref('');

    async function getUsers(num: number, gen: string) {
      loading.value = true;
      error.value = false;
      try {
        users.value = (await get(num, gen));
      } catch {
        error.value = true;
      } finally {
        loading.value = false;
      }
    }

    return {
      loading, error, getUsers, users, numOfUsers, gender,
    };
  },
});
