
export default function useUser() {
  async function get(num: number, gen: string) {
    const url = `https://randomuser.me/api/?inc=gender,name,location,email,picture&results=${num}&gender=${gen}&noinfo`;
    const response = await fetch(url)
      .then((res) => res.json())
      .then((res) => res.results);
    return response;
  }

  return {
    get,
  };
}
